import React, { useEffect, useState } from 'react';
import Form from 'react-jsonschema-form';
import "./App.css";
import CustomRangeSlider from './CustomRangeSlider';
import { Modal, show as showModal, Button, Box, Slider, Slide } from '@material-ui/core';

const schema = {
  type: 'object',
  properties: {
    range: {
      type: 'object',
      properties: {
        min: { type: 'number', title: 'Minimum Value' },
        max: { type: 'number', title: 'Maximum Value' },
      },
    },
  },
};
const uiSchema = {
  range: {
    'ui:widget': CustomRangeSlider,
  },
};

// const formData = {

//   range: {
//     min: 20,
//     max: 80,
//   },
// };



const App = () => {
  const [slidervalue, setslidervalue] = useState("");
  const [slidervalueOne, setslidervalueOne] = useState("");
  const [mins, setmins] = useState(slidervalue);
  const [maxs, setmaxs] = useState(slidervalueOne);

  const [datas, setdatas] = useState([]);
  const [formdata, setformdata] = useState([]);
  const [error, setError] = useState(false);



  // modal
  const [open, setopen] = useState(false);

  const handleshow = () => setopen(true);
  const handleclose = () => setopen(false);

  // modal end
  const formData = {

    range: {
      min: mins,
      max: maxs,
    },
  };


  useEffect(() => {
    // fetch("http://localhost:3000/product")
    fetch("https://fakestoreapi.com/products")
      .then(res => res.json())
      .then((resq) => {
        setdatas(resq);
        setformdata(resq);
        // console.log(resq);
      });
    console.log("renser")
  }, []);


  const handleFormChange = (data) => {
    console.log(data.formData.range.min);
    // setmins(data.formData.range.min);
    // setmaxs(data.formData.range.max);

    const filteredData = datas.filter((e) => {
      setError(false);
      handleclose();
      return e.price >= data.formData.range.min && e.price <= data.formData.range.max;
    });
    setformdata(filteredData);
    if (filteredData.length === 0) {
      setError(true);
    }
  };
  // handleSliderChange

  const handleSliderChange = (e, data) => {
    setslidervalue(data);
    setmins(data);
    console.log(mins);
    console.log(data + "data");

  }
  const handleSliderChangeOne = (e, data) => {
    setslidervalueOne(data);
    setmaxs(data);
  }


  return (
    <>


      <div className='w-100 bg-light shadow p-2 my-2'>
        <Button onClick={handleshow} className='border my-2 fs-6 fw-bold' ><i class="fa-solid pe-2 fa-filter"></i>Filter</Button>
      </div>
      <Modal
        className='d-flex justify-content-start transtion'
        open={open}
        onClose={handleclose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        {/* sx={style} */}
        <Box className='shadow bg-light p-3 rounded-3 box '>
          <h2 id="parent-modal-title"><i class="fa-solid fs-4 pe-2 fa-filter"></i>Filter</h2>
          <div>
            <div className='d-flex flex-row  gap-3 align-items-center '>
              <div>Min-$1</div>
              <div className='w-100 pt-2'>
                <Slider
                  value={slidervalue}
                  onChange={handleSliderChange}
                  max={1200}
                  min={0}
                  step={100}

                />
              </div>
            </div>
            <div className='d-flex flex-row w-100 gap-3 align-items-center '>
              <div>max-$1000</div>
              <div className='w-100 pt-2'>
                <Slider
                  value={slidervalueOne}
                  onChange={handleSliderChangeOne}
                  max={1200}
                  min={0}
                  step={100}
                />
              </div>
            </div>
          </div>
          <div className='filterWrapper w-100 d-flex justify-content-start px-3'>
            <div className='w-100'>
              <Form className='d-flex flex-column gap-3 py-2'
                schema={schema}
                uiSchema={CustomRangeSlider}
                formData={formData}
                onSubmit={handleFormChange}
              />
            </div>
          </div>
        </Box>
      </Modal>


      <div className='fluid-container bg-light-50 py-3 d-flex justify-content-center'>
        <div className='container '>
          <div className='row gy-2 d-flex ' >
            {error ? <h1>No items here....... please try again</h1> : formdata.map((e, keys) => (
              <div className='col' key={keys}>
                <div className="card cardBody m-auto" >
                  <div className='w-100 p-2 shadow'><img className="card-img-top p-2 " style={{ width: 100 + "%", height: 300 + "px" }} src={e.image} alt="Card image cap" /></div>
                  <div className="card-body">
                    <h5 className="card-title fs-5">{e.category}</h5>
                    <p className="card-text" style={{ width: 100 + "%", height: 100 + "px", overflow: "hidden" }}>{e.description}</p>
                    <p className="card-text  m-0 fs-4">${e.price}</p>
                    <a href="#" className="btn btn-warning">Add Card</a>
                  </div>
                </div>
              </div>
            ))};
          </div>
        </div>
      </div>
    </>
  );
};

export default App;
